* {
    font-family: 'Varela Round', sans-serif;
}

#home {
    min-height: 100vh;
    font-family: 'Varela Round', sans-serif;
}

#row {
    display: flex;
    justify-content: space-between; /* Divide the screen into two columns */
    align-items: center;
    padding: 20px 0 100px 0;
    background-image: url("../../../public/assets/Chicago1.jpeg"); /* Background image */
    background-size: cover; /* Ensure the background covers the entire section */
    background-position: center; /* Center the background image */
    background-repeat: no-repeat; /* Prevent background repetition */
    flex-direction: row;
    width: 100%;
}

.col {
    display: flex;
    flex-direction: row;
    width: 100%;
}

#leftside {
    width: 80%;
    padding-left: 30px;
    padding-right: 30px;
    position: relative;
    background-color: transparent;
}

#leftside h1 {
    font-size: 50px; /* Adjust heading font size */
    font-style: bold;
    font-weight: bolder;
    letter-spacing: 4px;
    line-height: 1.2; /* Increase line height for better readability */
    margin-bottom: 20px;
    display: block;
    color: var(--lighter-teal); /* Customize color variable */
    text-shadow: 0 0 6px #000000, 0 0 8px #0000FF; /* Text shadow effect */
}

#leftside p {
    font-size: 25px; /* Adjust paragraph font size */
    font-style: bold;
    font-weight: bolder;
    letter-spacing: 4px;
    display: flex;
    align-items: center;
    color: var(--slate); /* Customize color variable */
    text-shadow: 0 0 3px #00ffea, 0 0 5px #00ffea; /* Text shadow effect */
}

#rightside {
    padding-top: 22%;
    padding-right: 100px;
    position: relative;
    background-color: transparent;
}

/* Responsive design for smaller screens */
@media screen and (max-width: 900px) {
    #row {
        flex-direction: column;
        padding: 20px 0 50px 0;
    }

    .col {
        align-items: center;
        justify-content: center;
    }

    #leftside h1 {
        font-size: 40px; /* Smaller heading for mobile */
        font-style: bold;
        letter-spacing: 1px;
        line-height: 1.2; /* Maintain readability */
        margin-bottom: 20px;
        margin-top: 120px;
        display: block;
        color: var(--lighter-teal);
    }

    #leftside p {
        font-size: 20px; /* Smaller paragraph font size */
        font-style: bold;
        letter-spacing: 1px;
        display: flex;
        align-items: center;
        color: var(--slate);
    }

    #rightside {
        margin-left: 25%;
    }
}
