.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh; /* Full viewport height */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  margin: 0; /* Remove extra margins */
  padding-bottom: 10px; /* Minimal padding below the header */
}

h1 {
  font-size: 3rem; /* Adjust the heading font size */
  line-height: 1.2; /* Add spacing between lines */
  word-wrap: break-word; /* Prevent text overflow */
  margin: 0; /* Remove unnecessary margins */
}

p {
  font-size: 1.2rem; /* Adjust paragraph size */
  margin: 0; /* Remove default paragraph margins */
}

#research {
  margin-top: 10px; /* Minimal space between the header and research */
  padding-top: 0; /* Remove extra padding */
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@media (max-width: 768px) {
  h1 {
    font-size: 2.5rem; /* Adjust heading size for tablets */
  }
  p {
    font-size: 1rem; /* Adjust paragraph size for tablets */
  }
}

@media (max-width: 480px) {
  h1 {
    font-size: 2rem; /* Smaller heading for mobile devices */
  }
  p {
    font-size: 0.9rem; /* Smaller paragraph size for mobile devices */
  }
}
